.btn {
  transition: 333ms;
}
.btn.primary.text-start, .btn.text-start {
  text-align: left;
  padding-left: 1em;
}
.btn .icon {
  vertical-align: baseline;
  position: relative;
  height: 1.5em;
  width: 1.5em;
  margin: -0.25em 0.25em -0.25em 0;
}
.btn .icon2x {
  vertical-align: baseline;
  position: relative;
  height: 2em;
  width: 2em;
  top: -0.6em;
  margin: -1em 0.25em -1em -0.4em;
}

.btn.primary {
  background: linear-gradient(to bottom, #01636a 0%, #01404f 30%);
  color: #fff;
  padding: 0.5em 2em;
  border: 1px solid #01404f;
  box-shadow: #fff 0 0 5px -1px;
  text-decoration: none;
  text-transform: uppercase;
}
.btn.primary:focus, .btn.primary:hover {
  filter: brightness(1.3);
  background: linear-gradient(to bottom, #01636a 0%, #01404f 30%);
}
.btn.primary:active {
  background: linear-gradient(to bottom, #01636a 0%, #01404f 30%);
}
.btn.primary.hollow {
  background: transparent;
  border-color: 1px solid #01404f;
  box-shadow: inset #01404f 0 0 0 1px;
  color: #01404f;
}
.btn.primary.hollow:focus, .btn.primary.hollow:hover, .btn.primary.hollow:active {
  background: transparent;
  color: #01404f;
}

.btn.accent {
  background: #ec008c;
  color: #fff;
  padding: 0.5em 2em;
  border: 1px solid #ec008c;
  box-shadow: #fff 0 0 5px -1px;
  text-decoration: none;
  text-transform: uppercase;
}
.btn.accent {
  overflow: hidden;
  position: relative;
}
.btn.accent span {
  z-index: 20;
}
.btn.accent:after {
  background: #fff;
  content: "";
  height: 155px;
  left: -75px;
  opacity: 0.2;
  position: absolute;
  top: -50px;
  transform: rotate(35deg);
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  width: 50px;
  z-index: -10;
}
.btn.accent:hover:after {
  left: 120%;
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
}
.btn.accent:focus, .btn.accent:hover {
  filter: brightness(1.2);
  background: #ec008c;
}
.btn.accent:active {
  background: #ec008c;
}

