.toggle {
  display: block;
  cursor: pointer;
}
.toggle > div {
  width: 20px;
  height: 14px;
  position: relative;
}
.toggle > div div {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  transition: transform 0.5s ease;
}
.toggle > div div span {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
}
.toggle > div div span:first-child {
  top: 0;
}
.toggle > div div span:first-child:before, .toggle > div div span:first-child:after {
  top: 0;
}
.toggle > div div span:last-child {
  bottom: 0;
}
.toggle > div div span:last-child:before, .toggle > div div span:last-child:after {
  bottom: 0;
}
.toggle > div div span:before, .toggle > div div span:after {
  content: "";
  display: block;
  width: 47%;
  height: 2px;
  border-radius: 1px;
  background: #9ddbd9;
  position: absolute;
  -webkit-backface-visibility: hidden;
  transition: transform 0.5s ease, border-radius 0.3s ease, background 0.4s ease;
}
.toggle > div div span:before {
  left: 0;
  transform-origin: 0 50%;
  transform: translate(1px, 0) scaleX(1.1);
}
.toggle > div div span:after {
  right: 0;
  transform-origin: 100% 50%;
  transform: translate(-1px, 0) scaleX(1.1);
}
.toggle > div svg {
  display: block;
  fill: none;
  stroke: #9ddbd9;
  stroke-width: 2px;
  width: 44px;
  height: 44px;
  stroke-linecap: round;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -22px 0 0 -22px;
  stroke-dasharray: 0 82.801 8 82.801;
  stroke-dashoffset: 82.801;
  transform-origin: 50% 50%;
  -webkit-backface-visibility: hidden;
  transform: scale(1);
  transition: stroke-dashoffset 0.5s ease, stroke-dasharray 0.6s ease, transform 0.5s ease, stroke 0.4s ease;
}
.toggle > div svg:nth-child(3) {
  transform: rotate(180deg) scale(1);
}
.toggle.open > div div {
  transform: rotate(90deg);
}
.toggle.open > div div span:before, .toggle.open > div div span:after {
  background: #01404f;
}
.toggle.open > div div span:first-child:before {
  transform: rotate(45deg) translate(2.2px, -3px) scaleX(1.05);
}
.toggle.open > div div span:first-child:after {
  transform: rotate(-45deg) translate(-2.2px, -3px) scaleX(1.05);
}
.toggle.open > div div span:last-child:before {
  transform: rotate(-45deg) translate(2.2px, 3px) scaleX(1.05);
}
.toggle.open > div div span:last-child:after {
  transform: rotate(45deg) translate(-2.2px, 3px) scaleX(1.05);
}
.toggle.open > div svg {
  stroke-dashoffset: 62;
  stroke-dasharray: 0 82.801 62 82.801;
  transform: rotate(90deg);
  stroke: #01404f;
}
.toggle.open > div svg:nth-child(3) {
  transform: rotate(270deg);
}

