    left: 0;
    width: 100%;
    height: 100%;
    background-position: top right;
    background-repeat: no-repeat;
    z-index: 1;
  }
}
.grid-product.wide {
  width: 100%;
  grid-column-start: span 2;
}
.grid-product.wide img {
  position: absolute;
  width: 100%;
  object-fit: contain;
  transition: 333ms;
}
@media (min-width: 600px) {
  .grid-product.wide img {
    width: auto;
    right: auto;
    max-width: 73%;
  }
}
.grid-product.wide .grid-product__inner {
  padding-bottom: 100%;
  position: relative;
  z-index: 2;
}
@media (min-width: 600px) {
  .grid-product.wide .grid-product__inner {
    padding-bottom: 50%;
  }
}
.grid-product.wide .grid-product__name {
  text-align: left;
  padding-left: 1.5em;
}
.grid-product img {
  position: absolute;
  z-index: 2;
  height: calc(100% - var(--space-for-name));
  width: 100%;
  object-fit: contain;
  transition: 333ms;
}
.grid-product__inner {
  padding-bottom: 100%;
  position: relative;
  z-index: 3;
  bottom: 0;
  left: 0;
  right: 0;
}
.grid-product__name {
  font-size: 13px;
  font-weight: 300;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1em;
}
@media (min-width: 340px) {
  .grid-product__name {
    font-size: 14px;
  }
}
@media (min-width: 600px) {
  .grid-product__name {
    font-size: 20px;
  }
}
.grid-product:hover {
  color: #ec008c;
}
.grid-product:hover img {
  transform: scale(1.1);
}

.uppercase {
  text-transform: uppercase;
}

.block {
  display: block;
}

.relative {
  position: relative;
}

.overflow-hidden {
  overflow: hidden;
}

@media (min-width: 992px) {
  .columns-lg-2 {
    column-count: 2;
    column-gap: var(--gutter);
  }
}